<template>
  <main-tpl>
    
    <template v-slot:header>      
    </template>
    <template v-slot:left-side>
      <p>
      </p>
      <buyers-list />
       <tool-index />
      
    </template>

    <template v-slot:midnav>  
       <div class="flex-auto flex space-x-3">
          <button class="w-1/2 flex items-center justify-center bg-lime-300 hover:bg-yellow-300 text-black border border-black shadow-offset-black py-2" type="submit" @click="showTool">Add Tool</button>
          <button class="w-1/2 flex items-center justify-center border border-black hover:bg-yellow-300 shadow-offset-black py-2" type="button" @click="showBuyer">Register Buyer</button>
        </div>    
    </template>
    <template v-slot:center>
       
      <div v-if="buyer"><add-buyer /> </div>
       <div v-if="tool"><addTool /></div>
       <div v-if="invtry"><inventory /></div> 
    </template>
    <template v-slot:footer>
      <p>
      </p>
    </template>

     
  </main-tpl>
</template>

<script>
// @ is an alias to /src
import { mapGetters } from 'vuex';
import mainTpl from '@/components/templates/mainTemplate.vue'
import toolIndex from '@/components/allTools.vue'
import Inventory from '@/components/Inventory.vue'
import addTool from '@/components/addTool.vue'
import addBuyer from '@/components/addBuyer.vue'
import buyersList from '../components/buyersList.vue';
//import admin_template from 'admin_template'

export default {
  name: 'Home',
  data () {return {
    tool: false,
    buyer: false,
    invtry: true
  }},
  components: {
    mainTpl,
    toolIndex,
    Inventory,
    buyersList,
    addTool,
    addBuyer   
    
  },
  mounted() {
   
  },
  computed: {
    ...mapGetters({
      allTools: 'allTools',
      errorEvent: 'errorEvent'
      
    })
  },
  methods: {
    async showBuyer(){
      this.tool = false
      this.invtry = !this.invtry
      this.buyer = !this.buyer
    },
    async showTool(){
      this.tool = !this.tool
      this.invtry = !this.invtry
      this.buyer = false
    }

  }
}
</script>
