export default {
    state: {
        upload_message: null,
        messages: []
    },
    mutations: {
        ADD_U_MESSAGE (state, message) { state.upload_message = message },
        ADD_MESSAGE (state, message) { state.upload_message = message }
    },
    actions: {
        addUmessage(context, message) {
            context.commit('ADD_U_MESSAGE', message)
        }
    },
    getters: {
        UploadMessage: state => state.upload_message.uploaded_file,
        UploadUrl: state => state.upload_message.image_url

    }
}