import axios from 'axios';
let credo = 'gmtools:gxcityx776@'
var gmTools = 'http://' + location.hostname + ':7707'

export default {
    state: {
        tools: [],
        inventory: [],
        errorEvent: null
    },
    mutations: {
        ADD (state, data) {
            state.tools.push(data)
        },
        LOAD (state, data) {
            state.tools = data
        },
        LOAD_INVENTORY (state, data) {
            state.inventory = data
        },
        CLEAR (state) {
            state.tools = []
        },
        CLEAR_INVENTORY (state) {
            state.inventory = []
        },
        ERROREVENT (state, event) {
            state.errorEvent = event
        }
    },
    actions: {
        async addTool (context, toolData) {
            var url = `${gmTools}/tools/create/${toolData.type}`
            toolData.data.created = Date.parse(new Date())
            const res = await axios.post(url, toolData );
            console.log(res.data);           
            context.commit('ADD', res.data)
        },
        async loadTools (context) {
            var url = `${gmTools}/tools`
            axios.get(url)
            .then(response => {
                //console.log(response)
                context.commit('LOAD', response.data.tools)
            })
            .catch(error => {
                context.commit('ERROREVENT', error.messaage)
            })            
        },
        async loadInventory (context) {
            var url = `${gmTools}/tools/inventory`
            axios.get(url)
            .then(response => {
                //console.log(response)
                context.commit('LOAD_INVENTORY', response.data.tools)
            })
            .catch(error => {
                context.commit('ERROREVENT', error.messaage)
            })            
        }
    },
    getters: {
        AllTools: state => state.tools,
        Inventory: state => state.inventory,
        ErrorEvent: state => state.errorEvent
    }
}