<template>
    <div class="flex flex-col w-auto mt-10 mx-5">
       
            <ul class="mb-10">
                <li class="bg-gray-500 text-gray-200 px-3 py-3">Register New Buyer</li>
                <li>
                    <FormulateForm
                        v-model="purchaser"
                        @submit="submit"
                    >
                    <div class="double-wide">
                    <FormulateInput 
                        type="text"
                        label="Buyer's First Name"                        
                        name="firstname"
                        validation="required"
                        
                    
                    />
                    <FormulateInput 
                        type="text"
                        label="Last Name"
                        name="lastname"
                        validation="required"
                                            
                    />
                    </div>
                    <p class="text-sm font-semibold">Buyer's Contact Information</p>
                    <hr class="mb-2">
                    <FormulateForm v-model="purchaser.data.contact">
                        <div class="double-wide">
                        <FormulateInput 
                        type="text"
                        label="Telephone."                        
                        name="tel"        
                        />
                        <FormulateInput 
                        type="text"
                        label="Mobile"                        
                        name="mobile" 
                        validation="required"   
                        />
                        </div>
                        <FormulateInput 
                        type="text"
                        label="Email"                        
                        name="email"
                                        
                        />

                    </FormulateForm>
                        
                    
                    </FormulateForm>
                </li>
            
                <li>
                    <p class="text-sm font-semibold mt-2">Buyer's Address Information</p>
                    <hr class="mb-2">
                    <FormulateForm
                        v-model="purchaser.data.address"
                        
                    >
                    <div class="double-wide">
                    <FormulateInput 
                        type="text"
                        label="Lot"                        
                        name="lot"                        
                        class="w-20"
                    
                    />
                    <FormulateInput 
                        type="text"
                        label="Street"
                        name="street"
                    
                    />
                    </div>
                    <hr>
                    <div class="double-wide">
                    <FormulateInput
                        type="text"
                        label="Town/Village"
                        name="town"                        
                    />
                    <FormulateInput
                        type="text"
                        label="City/Parish"
                        name="city_parish"                        
                    />
                    </div>
                    <div class="double-wide">
                    <FormulateInput
                        type="submit"
                        label="Add Buyer"  
                        v-if="purchaser.data.contact.mobile"                      
                    />
                    <FormulateInput                        
                        type="button"
                        label="Cancel"  
                        @click.prevent="cancel"
                                        
                    />
                    </div>

                    </FormulateForm>
                </li>
            </ul>        
    </div>
</template>
<script>
export default {
    name: 'add-buyer',
    data () {
        return {
            purchaser: {
                firstname: null,
                lastname:null,
                data:{
                    address:  null,
                    contact: {
                        mobile: null,
                        email: null
                    }
                },
            
            }
        }       
    },
    methods: {
        submit() {            
            this.$store.dispatch('addPurchaser', this.purchaser) 
            this.clearForm()          
        },
        cancel() {           
            
            this.clearForm()          
        },
        clearForm () {
            this.purchaser = {
                firstname: null,
                lastname:null,
                data:{
                    address:  null,
                    contact: {
                        mobile: null,
                        email: null
                    }
                },
            
            }
        }
    }
}
</script>