<template>
    <div class="flex flex-col">
       
            <ul class="w-auto">
                <li>
                    <p class="flex flex-row bg-gray-500 text-gray-200 px-3 py-1">
                        Add New Tool
                    </p>
                </li>
                <li>
                    <FormulateForm
                        v-model="tool"
                        
                    >
                    <div class="tripple-wide">
                    <FormulateInput 
                        type="text"
                        label="Tool Name"                        
                        name="name"
                        validation="required"                    
                    />
                    <FormulateInput 
                        type="text"
                        label="Brand Name"
                        name="brand"                        
                    />
                    <FormulateInput 
                        type="text"
                        label="Category"
                        v-model="tool.data.category"
                        
                        />
                        
                    </div>
                     </FormulateForm>
                      <FormulateForm>
                    <div class="double-wide">
                    <FormulateInput 
                        type="text"
                        label="Model Number"
                        v-model="tool.data.model"                       
                    />
                    <FormulateInput 
                        type="text"
                        label="Serial Number"
                        v-model="tool.data.serial"  
                    />
                    </div>
                    <FormulateInput 
                        type="radio"
                        label="Power Supply"
                         v-model="tool.type" 
                         :options="{ power:'Power', battery: 'Battery', fuel:'Fuel'}"
                    />
                    <hr>
                   
                    </FormulateForm>
                </li>
            
                <li class="bg-gray-600 text-gray-100 px-3 py-3" v-if="tool.type">Tool Specifications
                    
                </li>
                 <li class="text-gray-800" v-if="tool.type =='battery'">
                    <FormulateForm>
                        <div class="tripple-wide">
                            <FormulateInput 
                            type="number"
                            label="Battery Voltage  V"
                            v-model.number="tool.specification.power.volts"  
                                                
                            />
                            <FormulateInput 
                                type="number"
                                label="Current  Ah"
                                v-model.number="tool.specification.power.current"
                            />
                       
                            <FormulateInput 
                                type="number"
                                label=" Batteries  Ea."
                                help="The Number of Batteries to operate tool, or in a Set"
                                v-model.number="tool.specification.power.amount"                        
                            />
                        </div>
                       
                    </FormulateForm>
                        
                    <FormulateForm>
                        <FormulateInput
                        type="textarea"
                        label="Special Details."
                        help="Separate special details with a comma."
                        v-model="toolprops"
                        ></FormulateInput>
                    </FormulateForm>
                </li>
                 <li class="mt-5" v-if="tool.type =='fuel'">
                    <FormulateForm>
                        <div class="tripple-wide">
                         <FormulateInput 
                            type="text"
                            label="Fuel Type Gas89 Gas90 E10 E13 Deisel"
                            v-model="tool.specification.power.fuel"                        
                            />
                        <FormulateInput 
                            type="number"
                            label="Fuel Capacity  l  ml"
                            help="This is the Fuel Tank Capacity."
                            v-model.number="tool.specification.power.fuel_capacity"
                            />
                        <FormulateInput 
                            type="text"
                            label="Lubricant  Oil-10 Oil-20 Oil-30 Oil-40"
                            help="The Machine Lubricating Oil."
                            v-model="tool.specification.power.lubricant"
                            />
                        </div>
                    </FormulateForm>
                    <FormulateForm>
                        <FormulateInput
                        type="textarea"
                        label="Special Details."
                        help="Separate special details with a comma."
                        v-model="toolprops"
                        ></FormulateInput>
                    </FormulateForm>

                </li>
                <li class="mt-5" v-if="tool.type =='power'">
                    <FormulateForm>   
                        <div class="tripple-wide">                
                         <FormulateInput 
                        type="number"
                        label="Voltage  V"
                        v-model.number="tool.specification.power.volts"
                         />
                     
                        <FormulateInput 
                            type="number"
                            label="Current Rating Ah"
                            v-model.number="tool.specification.power.current"
                            />
                        <FormulateInput 
                            type="number"
                            label="Voltage Cycle  Hz"
                            v-model.number="tool.specification.power.cycles"
                            />
                        </div>
                    </FormulateForm>
                    <FormulateForm>
                        <FormulateInput
                        type="textarea"
                        label="Special Details."
                        help="separate special details with a comma."
                        v-model="toolprops"
                         ></FormulateInput>
                    </FormulateForm>

                </li>
               
            </ul>
                <ul class="mt-5" v-if="tool.type">
                <li class="bg-gray-500 text-gray-200 px-3 py-3">Supplier and Stocking Details</li>
                 <li>
                    <FormulateForm>
                        <div class="double-wide">
                         <FormulateInput 
                        type="text"
                        label="Supplier."
                        v-model="tool.data.supplier"
                       />
                     
                     <FormulateInput 
                        type="number"
                        label="Stock Quantity"
                        v-model.number="tool.data.in_stock"
                        />
                        </div>
                    </FormulateForm>
                    
                </li>
                <li v-if="tool.type">
                    <FormulateForm>
                         <div class="double-wide">
                         <FormulateInput 
                        type="number"
                        label="Supply Price USD"                        
                        v-model.number="tool.price.supplier.usd"
                        />
                     <FormulateInput 
                        type="number"
                        label="Supply Price JAD"
                         v-model.number="tool.price.supplier.jad"
                         />
                         </div>
                    </FormulateForm>
                </li>
                 <li v-if="tool.type">
                    <FormulateForm>
                         <div class="double-wide">
                         <FormulateInput 
                        type="number"
                        label="Sale Price USD"
                        v-model.number="tool.price.sale.usd"
                    />
                     <FormulateInput 
                        type="number"
                        label="Sale Price JAD"
                        v-model.number="tool.price.sale.jad"
                    />
                    </div>
                    <div class="upload items-center">        
                        <FormulateForm           
                        @submit="onUpload"
                        >
                        
                                    <FormulateInput
                                    type="image"
                                    name="toolimage"
                                    :uploader="onFileSelected"
                                    label="Select Tool Image to upload"
                                    help="Select a png, jpg or gif to upload."
                                    validation="mime:image/jpeg,image/png,image/gif"                   
                                    />
                                    <div v-if="avatar">
                                        <img class="w-64" :src="avatar" alt="Image">
                                    </div>
                                    <FormulateInput
                                        type="submit"
                                        label="Upload Image"
                                    />                   
                        </FormulateForm>
                    
                    </div>

                    <FormulateInput
                        type="button"
                        label="Add Tool"
                        @click.prevent="submitTool"
                    />
                   
                    </FormulateForm>
                </li>
               
                <li> <p class="bg-yellow-400 text-gray-800 p-2">Uploaded {{tool}}</p>     </li>
            </ul>   
           
        
    </div>
</template>
<script>
import axios from 'axios'

export default {
    name: 'add-tool',    
    data () {
        return {       
            avatar: null,
            selectedFile: null,
            upload_progress: null,

            toolprops: null,
            tool: {
                name: null,
                brand:null,
                type:null,
                specification:{
                    power:{},
                    props:[]
                },
                data:{
                    supplier: null,
                    image_urls: []
                },
                price: {
                    supplier: {},
                    sale: {}
                }
            }
        }       
    },    
    methods: {  
        async onFileSelected (file, progress, error, options) {
            
            this.selectedFile = file
            
            let reader = new FileReader();
            reader.readAsDataURL(this.selectedFile);
            reader.onload = event => {
                this.avatar = event.target.result
            }
            this.upload_progress = progress(100)
        },
        async onUpload () {
            const self = this;
            const fd = new FormData()
            fd.append('image', self.selectedFile, self.selectedFile.name)  
            if(self.tool.data.model) {
                fd.append('model', self.tool.data.model)
            }         
            var url = "http://" + location.hostname + ":7707/tools/upload/" + self.tool.type
            axios.post(url, fd)//{'image': self.avatar})
            .then(response => {                
                self.tool.data.image_url = response.data.image_url 
                self.tool.data.image_urls.push(response.data.uploaded_file)
                console.log('YOU UPLOADED', response)

            })
            .catch(error => {
                console.log(error)
            })
        },      
        async submitTool () {
            if(this.toolprops) {
                this.tool.specification.props = this.toolprops.split(',')
            }
            this.$store.dispatch('addTool', this.tool)
            
        }
       
    }
}
</script>