import axios from 'axios';

var gmTools = 'http://' + location.hostname + ':7707'

export default {
    state: {
        suppliers: [],
        supplier: {},
        purchasers: [],
        purchaser: {},
        salesAgents: [],
        salesAgent: {},
        adminErrorEvent: ""
    },
    mutations: {
        ADD_purchaser (state, data) {
            state.purchaser = data
            state.purchasers.push(data)
        },
        LOAD_purchasers (state, data) {
            state.purchasers = data
        },
        CLEAR_purchaser (state) {
            state.purchaser = {}
        },
        CLEAR_purchasers (state) {
            state.purchasers = []
        },
        ERROR_event (state, event) {
            state.adminErrorEvent = event
        }
    },
    actions: {
        async addPurchaser(context, purchaser) {
            var url = `${gmTools}/tools/buyer/${purchaser.firstname}-${purchaser.lastname}`
            purchaser.data.created = Date.parse(new Date())
            const res = await axios.post(url, purchaser );
            console.log(res.data);           
            context.commit('ADD_purchaser', res.data)
        },
        loadPurchasers (context) {
            var url = `${gmTools}/tools/buyer`
            axios.get(url)
            .then(response => {
                context.commit('LOAD_purchasers', response.data)
            })
            .catch(error => {
                context.commit('ERROR_event', error.messaage)
            })
            
        }
    },
    getters: {
        buyers: state => state.purchasers,
        buyer: state => state.purchaser,
        adminErrorEvent: state => state.adminErrorEvent
    }
}