    <li>Buyers Index</li>
    <li v-for="buyer of buyers" :key="buyer.id" class="font-light border border-b py-3 px-4">
        {{buyer.id}} {{buyer.name}}
        
    </li>
    

</ul>
    
</template>
<script>
import { mapGetters } from 'vuex'

export default {
    name:"buyers",
    computed: {
        ...mapGetters({
            buyers: 'buyers'
        })

    }

}
</script>